html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
}
body.fijarbody {
  overflow: hidden;
  margin-right: 1.4rem;
}

p {
  font-size: 2rem;
  color: black;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  margin: 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.contenedor {
  max-width: 120rem;
  width: 95%;
  margin: 0 auto;
}

::-webkit-scrollbar {
  width: 1.4rem;
}

::-webkit-scrollbar-track {
  background-color: #F1F1F1;
}

::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 1rem;
}
::-webkit-scrollbar-thumb:hover {
  background-color: #555;
}

.bg-verde {
  background-color: #4CB8B3;
}

.bg-amarillo {
  background-color: #FEF46D;
}

.header {
  background-color: #4CB8B3;
  z-index: 2;
}
.header .contenido-header {
  padding: 2rem 0rem;
}
@media (min-width: 768px) {
  .header .contenido-header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header h1 {
  color: white;
  text-align: center;
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 0.2);
  margin-bottom: 1rem;
}
@media (min-width: 768px) {
  .header h1 {
    margin-bottom: 0rem;
  }
}
.header h1:hover {
  cursor: pointer;
}
.header.header-fijar {
  position: fixed;
  top: 0;
  box-shadow: 0 0.1rem 1rem #444;
  width: 100%;
}

.navegacion-principal {
  display: flex;
  gap: 0rem 0.8rem;
  justify-content: center;
}
.navegacion-principal a {
  color: white;
  font-size: 2rem;
  padding: 0.8rem;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  transition: background-color 0.3s ease-in;
}
.navegacion-principal a:hover {
  background-color: rgba(255, 255, 255, 0.4);
  transition: background-color 0.1s ease-in;
}

.video {
  position: relative;
  max-height: 70rem;
  overflow: hidden;
}
.video .overlay {
  position: absolute;
  width: 100%;
  height: 100%;
  /* Permalink - use to edit and share this gradient: https://colorzilla.com/gradient-editor/#891c1e+0,9e37f2+60,ffffff+100&0.8+0,0.5+100 */
  background: -moz-linear-gradient(top, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.62) 60%, rgba(255, 255, 255, 0.5) 100%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(top, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.62) 60%, rgba(255, 255, 255, 0.5) 100%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to bottom, rgba(137, 28, 30, 0.8) 0%, rgba(158, 55, 242, 0.62) 60%, rgba(255, 255, 255, 0.5) 100%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr="#cc891c1e", endColorstr="#80ffffff",GradientType=0 );
  /* IE6-9 */
}
.video video {
  display: block;
  width: 100%;
}

.contenido-video {
  display: flex;
  align-items: center;
  height: 100%;
  position: relative;
  z-index: 1;
}
.contenido-video h2 {
  color: white;
  font-size: 7rem;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.contenido-video h2 span {
  font-size: 2rem;
  background-color: #F53756;
  padding: 1rem 2rem;
  border-radius: 5px;
}
@media (max-width: 560px) {
  .contenido-video h2 {
    font-size: 10vw;
  }
  .contenido-video h2 span {
    font-size: 4vw;
    padding: 0.5rem 0.5rem;
  }
}

.festival {
  padding: 5rem 0rem;
}
@media (min-width: 768px) {
  .festival {
    display: grid;
    grid-template-columns: 1fr 1fr;
    column-gap: 5rem;
  }
}
.festival-img {
  width: 100%;
  height: 100%;
}

.festival-text {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
.festival-text h2 {
  margin-top: 1rem;
  padding: 1rem;
  border: 3px rgba(0, 0, 0, 0.5) dotted;
}
.festival-text span {
  font-size: 2rem;
  color: #F53756;
  font-weight: bold;
}
.festival-text__info {
  font-weight: lighter;
  line-height: 1.2;
}

.lineup {
  background-color: #F53756;
  padding: 2rem 0rem 4rem 0rem;
}
.lineup h3, .lineup .dia {
  text-align: center;
  color: white;
}
.lineup .dia {
  text-transform: uppercase;
  margin: 5rem 0rem;
}

@media (min-width: 768px) {
  .escenarios-contenedor {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}
.escenarios-contenedor .nombre-escenario {
  margin-top: 0;
  padding-top: 2rem;
  text-align: center;
  font-weight: lighter;
}
.escenarios-contenedor .nombre-escenario:after {
  content: "";
  display: block;
  width: 8rem;
  height: 6rem;
  margin: 1rem auto 0 auto;
  background-size: 150%;
  background-position: center center;
  background-repeat: no-repeat;
}

.calendario {
  list-style: none;
  font-weight: bold;
  padding: 0;
}
.calendario li {
  padding: 2.4rem;
}
.calendario span {
  font-weight: normal;
}

.bg-verde .calendario li:nth-child(odd) {
  background-color: #3b9691;
}

.bg-amarillo .calendario li:nth-child(odd) {
  background-color: #fdec08;
}

.edm .nombre-escenario:after {
  background-image: url("../img/icono_edm.png");
}

.rock .nombre-escenario:after {
  background-image: url("../img/icono_rock.png");
}

.galeria {
  padding: 5rem 0;
}
.galeria h3 {
  text-align: center;
}

.galeria-img {
  list-style: none;
  display: grid;
  grid-template-columns: 1fr 1fr;
  justify-items: center;
  gap: 2rem;
  padding: 2rem;
}
@media (min-width: 768px) {
  .galeria-img {
    grid-template-columns: repeat(4, 1fr);
  }
}
@media (min-width: 1200px) {
  .galeria-img {
    grid-template-columns: repeat(6, 1fr);
  }
}

.galeria-everyimg {
  border: 1px solid rgba(255, 255, 255, 0);
}
.galeria-everyimg:hover {
  cursor: pointer;
  border: 1px solid black;
  transition: border 0.2s ease;
}

.galeria-overlay {
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: grid;
  place-items: center;
}

.galeria-overlay__button {
  width: 5rem;
  height: 5rem;
  font-size: 3rem;
  font-weight: 900;
  background-color: white;
  border: 1px solid black;
  border-radius: 50%;
  display: grid;
  place-items: center;
}
.galeria-overlay__button:hover {
  cursor: pointer;
}

.boletos {
  padding: 2rem 0;
}
.boletos h3 {
  color: white;
  text-align: center;
}
.boletos p {
  color: white;
  text-align: center;
  text-transform: uppercase;
  margin: 3rem 0 1rem 0;
}
@media (min-width: 480px) {
  .boletos .contenedor-boletos {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
  }
}

.pase {
  border-spacing: 0;
  width: 100%;
}
.pase thead {
  background-color: #F53756;
}
.pase thead th {
  color: white;
  font-size: 2rem;
  font-weight: 300;
  text-transform: uppercase;
  padding: 1rem;
}
.pase tbody {
  background-color: white;
}
.pase tbody td {
  padding: 0.8rem;
  border-bottom: 1px solid #d1d1d1;
}
.pase tbody tr:last-of-type td {
  border-bottom: none;
}
.pase tfoot {
  color: white;
  background-color: #752F97;
  text-align: center;
  border-radius: 5px;
}
.pase tfoot td {
  font-size: 2rem;
  font-weight: 600;
  padding: 1.2rem;
}
.pase tfoot td:hover {
  cursor: pointer;
}

.footer {
  background-color: #F53756;
  padding: 1rem;
  text-align: center;
}
.footer p {
  color: white;
  margin: 0;
}