@mixin mq($px) 
{
    @media(min-width: $px) { @content; }
}

@mixin tablet
{
    @media(min-width: 768px) { @content; }
}

@mixin desktop
{
    @media(min-width: 1200px) { @content; }
}

@mixin grid($columnas, $espaciado)
{
    display: grid;
    grid-template-columns: repeat($columnas, 1fr);
    gap: $espaciado;
}